.SearchForm {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 20px;
  padding: 8px;
  border-radius: 2px;
  border: 3px solid #424242;
}

.SearchForm__input {
  padding: 6px;
  font-size: 20px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  color: #424242;
  border: 0;
  border-radius: 6px;
  outline: none;
}



