.StackList {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  list-style: none;
}

.StackList__item {
  width: 13%;
  padding: 10px;
  border: 3px solid #424242;
  border-radius: 4px;
}

.StackList__icon {
  display: block;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
}



