.CareerInfo {
  margin: auto;
  border-collapse: collapse;
}

.CareerInfo caption {
  color: #424242;
  margin-bottom: 20px;
  font-size: 24px;
}

.CareerInfo td {
  padding: 12px 24px;
  border: 3px solid #424242;
  font-size: 18px;
  color: #424242;
}

.CareerInfo ul {
  list-style: none;
}

.CareerInfo li {
  padding: 6px 4px;
}



